@import

"../base/mixin",
"../base/variable";

//clearfix
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}

html,body{
  height: 100%;
}
body{
  background: #f8f8f8;
  color: #333;
}
img {
  max-width: 100%;
  vertical-align: middle;
  -ms-interpolation-mode: bicubic;
}
.isno{
  display: none;
}
#header{
  margin-bottom: 10px;
  background: #f8f8f8;
  position: relative;
  .user-icon{
    position: absolute;
    top: 0;
    right: 10px;
    i{
      color: $base-color;
    }
  }
}
.top-header{
  width: 100%;
  height: 45px;
  line-height: 45px;
  h2{
    font-size: 18px;
    font-weight: bold;
    padding-left: 10px;
    font-family: "Microsoft YaHei";
    a{
      color: #333;
    }
  }
  .sunflower{
    display: inline-block;
    height: 30px;
    width: 30px;
    margin-top: -7px;
    vertical-align: middle;
    background-repeat: no-repeat;
    background-position: center;
    -webkit-background-size: contain;
    background-size: contain;
    background-image: url("../img/icon/iconfont-augichuahou.svg");
  }
}

.nav-icon-bg{
  width: 43px;
  height: 43px;
  display: inline-block;
  background: $base-color;
  border-radius: 100%;
  color: #fff;
  position: relative;
}
.nav-text{
  display: block;
  text-align: center;
  font-size: 12px;
  color: #333;
}
.main-nav{
  margin: 10px auto;
  padding: 15px 0;
  background: #FFF;
  .ui-icon{
    color: #fff;
  }
}
.nav-icon{
  display: inline-block;
  height: 26px;
  width: 26px;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -13px;
  margin-top: -13px;
  background-repeat: no-repeat;
  background-position: center;
  -webkit-background-size: contain;
  background-size: contain;
}

.huodong-icon{
  background-image: url("../img/iconfont-huodong.svg");

}
.tuandui-icon{
  background-image: url("../img/icon/iconfont-tuandui.svg");
}
.ketang-icon{
  background-image: url("../img/icon/iconfont-weiketang.svg");
}
.anli-icon{
  background-image: url("../img/icon/iconfont-yuanjiaojuxing6kaobei.svg");
  -webkit-background-size: 90% auto;
  background-size: 90% auto;
}
.shopping-icon{
  background-image: url("../img/icon/iconfont-shangcheng.svg");
}
.jobs-icon{
  background-image: url("../img/icon/iconfont-zhaopin.svg");
}
.weibo-icon{
  background-image: url("../img/icon/iconfont-weibo.svg");
}
.about-icon{
  background-image: url("../img/icon/iconfont-guanyuwomen.svg");
}

.ui-wrap{
  padding-bottom: 70px;
}
.grid-nav-1{
  margin-bottom: 15px;

  li{
    &:nth-child(2){
      .nav-icon-bg{
        background: #84D702;
      }
    }
    &:nth-child(3){
      .nav-icon-bg{
        background: #FF7C02;
      }
    }
    &:nth-child(4){
      .nav-icon-bg{
        background: #5297DF;
      }
    }
  }
}
.grid-nav-2{

  li{
    &:nth-child(1){
      .nav-icon-bg{
        background: #8B49FF;
      }
    }
    &:nth-child(2){
      .nav-icon-bg{
        background: #00D69A;
      }
    }
    &:nth-child(3){
      .nav-icon-bg{
        background: #FFB224;
      }
    }
    &:nth-child(4){
      .nav-icon-bg{
        background: #15A7FF;
      }
    }
  }
}


.img-nav{
  background: #FFF;
  margin-bottom: 10px;
  padding:10px 10px 10px 15px;

  ul{
    overflow: hidden;
  }
  li{
    width: 50%;
    float: left;
    //background: #3d98ff;

    &:nth-child(1){
      //background: #ff697a;
    }
    &:nth-child(2){
      //background: #3d98ff;
    }
    a{
      color: #FFF;
    }
  }
}
.img-nav-item{
  background: #FFF;
  li{
    position: relative;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding-right: 5px;

    a{
      display: block;
      background-repeat: no-repeat;
      -webkit-background-size: cover;
      background-position: center;
      padding-top: 75.85%;

    }

  }
}
.img-nav-2{

  .img-nav-item{
    li{
      &:nth-child(1){
        //background: #AAC700;
      }
      &:nth-child(2){
        //background: #FAC300;
      }
      a{
        padding-top: 41.38%;
      }
    }
  }

}

.mod-box{
  background: #FFF;
  margin-bottom: 10px;
}
.mod-hd{
  position: relative;
  padding: 10px 0;
  line-height: 25px;

  h3{
    display: inline-block;
    font-size: 16px;
    padding-left: 25px;
    &:before{
      content: " ";
      position: absolute;
      width: 5px;
      height: 25px;
      background: $base-color;
      left: 10px;
      top: 10px;
      display: inline-block;
      
    }
  }
  a{
    width: 100%;
    font-size: 14px;
    position: absolute;
    left: 0;
    top: 10px;
    display: block;
    text-align: right;
    span{
      display: inline-block;
      margin-right: 10px;
    }
  }
}
.mod-bd{
  padding: 0;
}
.ui-one{
  padding: 10px 15px 0;
  li{
    padding-bottom: 10px;
  }
  .ui-placehold-img{
    padding-top: 18.75%;
    //100
  }
}
.ui-four{
  padding:10px 10px 10px 15px;
  >li{
    padding-right: 5px;
    &:nth-child(1){
      span{
        background-color: #9342F3;
      }
    }
    &:nth-child(2){
      span{
        background-color: #FFB217;
      }
    }
    &:nth-child(3){
      span{
        background-color: #089792;
      }
    }
    &:nth-child(4){
      span{
        background-color: #D64A81;
      }
    }
  }
  .ui-placehold-img{
    padding-top: 100%;
    //145
    
    span{
      color: #FFF;
      text-align: center;
      font-size: 14px;
      line-height: 500%;
    }
  }
}
.ui-img{
  padding-bottom: 10px;
  .ui-placehold-img{
    padding-top: 68.97%;
  }
  .ui-col:nth-child(1){
    padding-right: 10px;
  }
  .ui-img-infor{
    font-size: 14px;
    padding-right: 20px;
  }
}
.index-footer{
  height: auto;
}
.footer-nav{
  padding: 0 0 5px;
  font-size: 12px;
}
@media (max-width: 320px) {
  .ui-four,.img-nav{
    padding: 10px 5px 10px 10px;
  }
  .ui-one{
    padding: 10px 10px 0;
  }
}


// user center
.user-main{
  padding-top: 45px;
}
.uc-item{
  .ui-list{
    margin-bottom: 10px;
    li{
      a{
        color: #666;
      }
      span{
        display: inline-block;
        line-height: 44px;
        font-size: 14px;
        margin-left: 6px;
      }
      i{
        font-size: 26px;
      }
    }
  }
}

//login register
.login-box{
  padding: 75px 0 0;
  .login-form{
    border-radius: 4px;
    margin: 0 10px;
  }
  .reset-pwd{
    text-align: right;
    padding: 15px 20px 0;
    font-size: 14px;
    position: relative;
  }
  .remember-user{
    padding: 15px 20px 0;
    font-size: 14px;
  }
  .ui-icon-close{
    display: none;
  }
  .ui-item-mb10{
    margin-bottom:10px;
  }
}

.uc-edit{
  padding-top:10px;

  .ui-form{
    margin: 0 10px;
    border-radius: 4px;
  }
  .ui-icon-close{
    display: none;
  }
}

.ui-form-item-file{
  margin: 0 10px;
}
.bef-file{
  position: relative;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-pack: center;
  font-size:16px;

  .ui-icon-add{
    color: #FFF;
  }

  input {
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    opacity: 0;
    z-index: 999;
  }
}

.avatar-upload{
  display: block;
  width: 80px;
  height:80px;
  border-radius: 80px;
  background: #e5e5e5;
  color: #666;
  text-align: center;
  margin: 0 auto 20px;
  position: relative;
  overflow: hidden;
  font-size: 12px;
  div{
    margin-top: -5px;
  }
  .ui-icon-add-people{
    display: inline-block;
    margin-top:10px;
  }
  input {
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    opacity: 0;
    z-index: 999;
  }
}


.user-comment-item{
  margin: 0 10px;
  padding: 10px 0;
  .label{
    display: block;
    color: #666;
    padding: 10px 0;
  }
  .textarea {
    height: 150px;
    font-size: 14px;
    width: 100%;
    resize: none;
    display: block;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    border: none;
    background: #FFF;
    padding: 15px 10px;
    color: #333;
    border-radius: 4px;
  }

}
.list-box{
  padding-top:55px;
  background: #FFF;
}
.list-hd{
  position: relative;
  padding: 10px 10px 0;
  line-height: 22px;

  h3{
    display: inline-block;
    font-size: 14px;
  }
  a{
    width: 100%;
    font-size: 12px;
    position: absolute;
    left: 0;
    top: 10px;
    display: block;
    text-align: right;
    span{
      display: inline-block;
      margin-right: 10px;
    }
  }
}
.list-bd{
  padding: 10px 0 0;
}

.case-list{
  >li{
    a{
      display: block;
    }
  }
  .topic-img{
    margin-bottom: 10px;
  }
}

.list-nav{
  background: #FFF;
  margin:10px 0;
  padding: 20px 0;
  .list-nav-box1{
    margin-bottom:20px;
    li{
      a{
        display: block;
        background: #44A3FF;
        color: #FFF;
        padding: 7px 14px;
      }
      &:nth-child(2){
        a{
          background: #FF0C4D;
        }
      }
      &:nth-child(3){
        a{
          background: #00D69A;
        }
      }
    }
  }
  .list-nav-box2{
    li{
      a{
        display: block;
        background: #8B49FF;
        color: #FFF;
        padding: 7px 14px;
      }
      &:nth-child(2){
        a{
          background: #FFB224;
        }
      }
      &:nth-child(3){
        a{
          background: #397bcc;
        }
      }
    }
  }

}

.clearPT{
  padding-top:0;
}
.jobs-detail{
  padding: 20px 20px 30px;
  font-size:14px;
  h4{
    font-size:16px;
    margin-bottom:10px;
  }
  p{
    margin-bottom:10px;
  }
}



#footer{
  background: #FFF;
  padding: 15px 0 20px;
  text-align: center;
  font-size: 12px;
  color: #777;
  margin: 10px auto 0;
}
.list-jobs{
  .ui-list-img{
    width: 110px;
    height: 100px;
  }
  .ui-btn{
    font-size:12px;
    height:25px;
    line-height:25px;
    margin-top:3px;
  }
}
.uc-about{
  margin-top: 10px;
  background: #FFF;
  padding: 10px;
  font-size: 14px;
  color: #666;
  p{
    text-indent: 24px;
    margin-bottom:10px;
  }
}


.main-sub-nav{
  padding-top:55px;
}
// old

.search-bar{
  padding-top: 45px;
}
.go-home{
  position: absolute;
  display: inline-block;
  right: 10px;
  top: 0;
}

.list-conts{
  li:after{
    display: none;
  }
  .ui-label-list{
    margin: 0;
  }
  .list-other{
    position: relative;
    line-height: 27px;
  }
  .ui-price{
    position: absolute;
    right: 0;
    top: 0;
    em{
      display: inline-block;
      font-size: 18px;
      margin-right: 5px;
    }
  }
  .topic-icon{
    position: absolute;
    top: 0;
    left: -5px;
    font-size: 12px;
    padding: 0 10px 0 15px;
    color: #fff;
    background: rgba(239, 75, 57,.9);
    -webkit-transform: skew(-20deg);
    -moz-transform: skew(-20deg);
    -o-transform: skew(-20deg);
    border-radius: 0 0 5px 0;
    &.cu-bg{
      background: rgba(158, 217, 105, .9);
    }
    &.zs-bg{
      background: rgba(139, 73, 255, .9);
    }
    &.ls-bg{
      background: rgba(0, 214, 154, .9);
    }
    &.hs-bg{
      background: rgba(255, 178, 36, .9);
    }
  }
}
.ui-time{
  color: #999;
}
.ui-btn-row{
  padding: 6px 10px;
}
.search-box{
  padding-top: 45px;
}
.hove-time{
  overflow: hidden;
  zoom: 1;
  line-height: 25px;
  padding:10px 0 10px 20px;
  strong{
    color: #999;
  }
}
.more-bar{
  text-align: center;
  font-size: 16px;
  a{
    color: #666;
  }
  .ui-icon-more{
    display: inline-block;
    font-size: 22px;
  }
}
.detail-top{
  background: #FFF;
  padding: 10px 0;
  text-align: center;
  h2{
    padding: 10px;
  }
}

.detail-content{
  background: #FFF;
  padding: 0 10px 30px;
  p{
    margin-bottom:15px;
  }
}
.detail-content-star{
  padding: 30px;
  p{
    margin-bottom:10px;
    &.user-intro{
      padding-top:20px;
    }
  }
}

.detail-rec{
  .ui-list-img{
    width: 136px;
    height:136px;
  }
}
.user-topic{
  background: #FFF;
  padding: 10px 10px 30px;
  font-size: 14px;
}
.topic-item{
  padding-bottom:10px;
  margin-bottom:10px;

  ul{
    overflow: hidden;
    padding: 10px 0;
    li{
      float: left;
      width:50%;
    }
  }

}

.popup-box{
  .ui-icon-close {
    display: none;
  }
  .ui-form{
    background: none;
  }
}